  height: 93px;
  padding-right: 50px;
  display: flex;
  box-shadow: 0 3px 6px #00000029;
}

.intro .nav .logo_container {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  width: auto;
  padding: 30px;
  display: flex;
}

.intro .nav .logo_container .logo_nav {
