.intro .nav .menu_container {
  text-transform: uppercase;
  align-items: center;
  width: auto;
  height: 93px;
  font-size: 16px;
  display: flex;
}

.intro .nav .menu_container .hamburger {
  cursor: pointer;
  float: right;
  border-top: 4px solid #fff;
  border-bottom: 4px solid #fff;
  outline: none;
  width: 30px;
  height: 30px;
  margin: 7px 0 0;
  display: none;
  position: relative;
}

.intro .nav .menu_container .hamburger .checkbox {
  visibility: none;
}

.intro .nav .menu_container .hamburger:after, .intro .nav .menu_container .hamburger:before {
  content: "";
  background: #fff;
  width: 30px;
  height: 4px;
  display: block;
  position: absolute;
  top: 10px;
}

@media screen and (width <= 900px) {
  .intro .nav .menu_container .nav-list-link {
    display: none;
  }

  .intro .nav .menu_container .nav {
    justify-content: center;
  }

  .intro .nav .menu_container .hamburger {
    display: block;
  }

  .intro .nav .menu_container .hamburger .checkbox {
    display: none;
  }
}

.intro .nav .menu {
  display: flex;
}

.intro .nav .menu .menu_item {
  justify-content: space-between;
  list-style-type: none;
}

.intro .nav .menu .menu_item .nav-list-link {
